import React, { Component } from "react";

class Card extends Component {
    constructor(props) {
        super(props);

        this.card = React.createRef();
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        this.props.handleClick(this.props.project.header);

        const gallery = document.getElementById("gallery");

        if(gallery.className == "card-deck mobile")
            return
        
        const gal_box = gallery.getBoundingClientRect();
        const style = getComputedStyle(this.card.current);
        const yProp = style.getPropertyValue('--top');
        const initY = parseInt(yProp.substring(0, yProp.length - 2));

        const width = gal_box.width * 5 / 6;
        const x = gal_box.width/2 - width / 2;
        const y = initY * gal_box.height / (gal_box.height + width * 2);

        const isFocus = this.props.focus != null;

        if(!isFocus) {
            this.card.current.style.left = `${x}px`;
            this.card.current.style.top = `${y}px`;
            this.card.current.style.width = `${width}px`;
        } else {
            this.card.current.style.left = null;
            this.card.current.style.top = null;
            this.card.current.style.width = null;
        }
    }

    render() {
        let cardClass = this.props.project.size + " card";
        const isFocus = this.props.focus != null;
        cardClass += isFocus ? (this.props.focus == this.props.project.header ? " focus" : " hidden") : " gallery";

        const hasLink = this.props.project.link != null;

        return (
            <div ref={ this.card } className={ cardClass }>
                
                <div className="card-body" onClick={ this.handleClick } style={{backgroundImage: `url(${this.props.project.img})`}}>
                    <div className="card-overlay"></div>
                    <h2 className="card-header text-header">{  this.props.project.header  }</h2>
                </div>

                <div className="card-desc text-body scale-text-body">
                    <p><i>{ this.props.project.tag }</i></p>
                    <p>{ this.props.project.desc }</p>
                    <p className="card-link">
                        {hasLink
                            ? <a href={ this.props.project.link }>- visit project -</a>
                            : <span className="no-link">- no link yet -</span>
                        }
                    </p>
                </div>
            
            </div>
        );
    }
}

export default Card;