class Deck extends Component {
    constructor(props) {
        super(props);
        this.state = { focus: null, isDesktop: null }; 

        this.ref = React.createRef();
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick(project) {
        const isDesktop = this.ref.current.className.indexOf("mobile") == -1;

        if(project == this.state.focus) 
            project = null;

        if(isDesktop && this.state.focus != null && project != null)
            return;

        this.setState({
            focus: project,
        });
    }

    render() {
        return (
            <div className="card-deck" id="gallery" ref={ this.ref }>
                {projects.map((proj) => (
                    <Card focus={ this.state.focus } handleClick={ this.handleClick } key={ proj.header } project={ proj }/>
                ))}
            </div>
        );
    }
}

export default Deck;