import React, { Component } from "react";
import ReactDOM from "react-dom";
import Splash from "./splash.js";
import Deck from "./deck.js";
import "./splash.css";
import "./card.css";
import "./footer.css";
import "./style.css";

class App extends Component {
    componentDidMount() {
        let script = document.createElement("script");
        script.async = true;
        script.src = "anim.js";
        document.body.appendChild(script);
    }

    render() {
        return (
            <div className="content-container">
                <canvas id="bg-anim"/>
                <Splash />
                <h2 className="section-header">Projects:<div className="header-underline"></div></h2>
                <Deck />
                <div className="footer-wrapper">
                    <h2 className="section-header">Contact Me:<div className="header-underline"></div></h2>
                    <p className="footer-content text-body scale-text-body">
                        Email: &emsp;&emsp;ianedougherty01@gmail.com<br/>
                        Resume: &ensp;&nbsp;<a className="resume-link" href="https://idougherty.github.io/portfolio/ian's_resume.pdf" target="_blank" rel="noreferrer">Click to download</a><br/>
                    </p>
                </div>
            </div>
        )
    }
}

ReactDOM.render(<App />, document.getElementById('root'))

const CARD_SIZES = {
    "sm card": 2,
    "md card": 3,
    "lg card": 4,
}


function calcSpacing() {
    let G_WIDTH = 6;
    let gal_width;

    gal_width = visualViewport.height * .5;

    let card_size = gal_width * .9 / G_WIDTH;
    let card_spacing = gal_width * .1 / Math.max(G_WIDTH - 1, 1);
    
    buildGallery(G_WIDTH, card_size, card_spacing);

    if(visualViewport.width < 768) {
        gallery.className = "card-deck mobile";
        gallery.style.height = null;//(250 + card_spacing) * gallery.children.length + "px";
    } else {
        gallery.className = "card-deck gallery";
    }
}

let gallery = document.getElementById("gallery");
new ResizeObserver(calcSpacing).observe(gallery);

calcSpacing();

function buildGallery(G_WIDTH, card_size, card_spacing) {
    // const gal_box = gallery.getBoundingClientRect();
    // let offsetX = page.

    let x0 = 1;
    let y0 = 1;
    
    let row = [];
    let row_height = 0;
    let row_width = 0;
    let area = 0;

    for(const element of gallery.children) {
        row.push(element);
        let size = CARD_SIZES[element.className.substring(0, 7)];

        if(G_WIDTH == 1)
            size = 1;

        area += size * size;
        row_width += size;
        row_height = Math.max(size, row_height);
    
        //place elements
        if(row_width >= G_WIDTH && G_WIDTH * row_height == area) {
            let x = x0;
            let y = y0;
    
            for(const card of row) {
                let size = CARD_SIZES[card.className.substring(0, 7)];
    
                if(G_WIDTH == 1)
                    size = 1;
                    
                const nx = (x - 1) * card_size + (x - 1) * card_spacing;
                const ny = (y - 1) * card_size + (y - 1) * card_spacing;
                const nsize = card_size * size + (size - 1) * card_spacing;
    
                card.style.setProperty('--left', Math.floor(nx) + 'px');
                card.style.setProperty('--top', Math.floor(ny) + 'px');
                card.style.setProperty('--width', Math.floor(nsize) + 'px');

                y += size;
    
                if(y + size > y0 + row_height) {
                    x += size;
                    y = y0;
                }
            }
    
            x0 = 1;
            y0 += row_height;
    
            row = [];
            row_width = 0;
            row_height = 0;
            area = 0;
        }
    }

    gallery.style.height = y0 * card_size + (3) * card_spacing + "px";
}

document.onmousemove = (e) => {
    if(gallery.className == "card-deck mobile")
        return

    let card_focus = document.getElementsByClassName("focus")[0];
    
    if(!card_focus)
        return;

    let focus = card_focus.children[0];

    let x = e.clientX;
    let y = e.clientY;
    
    let box = focus.getBoundingClientRect();

    let calcX = (x - box.x - (box.width / 2)) / 100;
    let calcY = -(y - box.y - (box.height / 2)) / 100;

    let length = Math.sqrt(calcX * calcX + calcY * calcY);

    if(length > 4) {
        calcX = calcX / length * 4;
        calcY = calcY / length * 4;
    }

    focus.style.transform = `rotateX(${calcY}deg) rotateY(${calcX}deg)`;
    focus.getElementsByClassName("card-overlay")[0].style.backgroundPosition = `${50 - 7*calcX}% ${70 - 7*calcY}%`;
};